import React, { useState } from "react";
import logo from "./logo.svg";
import "./App.css";
import Player from "./Player";
import { Box, createStyles, Grid, makeStyles, Button } from "@material-ui/core";

const useStyles = makeStyles((theme) =>
  createStyles({
    content: {
      width: "100%",
      padding: "1.5rem 1.5rem 0 1.5rem",
      background: "#FAFAFA",
      [theme.breakpoints.down("xs")]: {
        padding: "0",
        background: "#000000",
        overflow: "hidden",
      },
    },
    grid: {
      [theme.breakpoints.down("xs")]: {
        paddingBottom: "0px !important",
      },
    },
    overlayContainer: {
      "& .quiz-btn-container": {
        position: "absolute",
        top: 20,
        right: 20,
        zIndex: 4,
        opacity: 0,
        transition: "opacity .25s ease-in-out",
      },
      "&:hover .quiz-btn-container": {
        opacity: 1,
      },
    },
  })
);

function App() {
  const classes = useStyles();
  const [conditionalRender, setConditionalRender] = useState(false);
  return (
    <Grid container spacing={4}>
      <Grid item xs={12} md={8} className={classes.grid}>
        {conditionalRender && (
          <Box position="relative" className="App">
            <div className="card">
              <Player />
            </div>
          </Box>
        )}
      </Grid>
      <Box>
        <Button onClick={() => setConditionalRender(!conditionalRender)}>
          Render video
        </Button>
      </Box>
    </Grid>
  );
}

export default App;
